.head {
  width: 1248px;
  height: 84px;
  margin-top: 50px;
  margin-bottom: 16px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #426486;
  border-radius: 16px;
  padding: 10px 10px;
  position: relative;
  margin: auto;
}

.head h1 {
  font-family: Allerta;
  color: #f5f5f5;
  font-weight: 400;
  font-size: 35px;
  /* line-height: 40.5; */
}
.head h3 {
  font-family: Allerta;
  color: #f5f5f5;
  font-weight: 400;
  font-size: 25px;
}
.menu {
  display: flex;
  gap: 35px;
}
.menu img {
  width: 150px;
  height: 50px;
  border-radius: 15px;
}
.menu img:hover {
  box-shadow: 0 0px 5px 3px #edd178;
}

.menu a {
  color: #f5f5f5;
  font-family: Allerta;
}
@media (width < 600px) {
  .head {
    width: 580px;
  }
}
@media (width < 480px) {
  .head {
    width: 450px;
    height: 120px;
    padding: 10px 0;
  }
}
@media (width < 320px) {
  .head {
    width: 300px;
    flex-direction: column;
    justify-content: center;
    height: 100px;
    margin: auto;
  }
}
