.educationList {
  background: #426486;
  width: 1248px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  margin: 16px auto;
  align-items: center;
  text-align: left;
}
.education {
  display: flex;
}
.addEducation {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
}
.edu {
  padding: 20px;
  width: 400px;
}
.edu ul,
li {
  list-style: none;
}
@media (width < 600px) {
  .education {
    flex-direction: column;
    text-align: center;
  }
  .edu {
    width: 590px;
    padding: 5px;
  }
  .educationList {
    width: 590px;
    text-align: center;
  }
}
@media (width < 480px) {
  .education {
    flex-direction: column;
    text-align: center;
  }
  .edu {
    width: 450px;
    padding: 5px;
  }
  .educationList {
    width: 470px;
    text-align: center;
  }
}
@media (width < 320px) {
  .education {
    flex-direction: column;
    align-items: center;
  }
  .edu {
    width: 290px;
    text-align: center;
  }
  .educationList {
    width: 290px;
  }
}
