.container_slide {
  height: 15rem;
  background-color: #426486;
  margin: 10px;
  padding: 0.5rem;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.container_slide img {
  border-radius: 15px;
  width: 100%;
  height: 100%;
  outline: none;
  overflow: hidden;
  transition: transform 600ms ease-in-out;
}

.desc {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  left: 0;
  bottom: -20rem;
  padding: 15px;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
  transition: all 400ms ease-in-out;
  width: 100%;
  height: 100%;
}
.desc h1 {
  font-size: 26px;
  font-family: Allerta, sans-serif;
  /* color: #e9c564; */
}

.desc p {
  width: 90%;
  font-size: 20px;
}
.desc a {
  margin-left: 0.4rem;
  color: #edd178;
}
:hover > .container_slide img {
  transform: scale(1.5);
  filter: opacity(25%);
}
:hover > .desc {
  bottom: -10px;
}

@media (width < 600px) {
  .container_slide {
    height: 18rem;
  }
}
