* {
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: DMSans_bold;
  src: url(./Components/Fonts/DMSans-Bold.ttf);
}
@font-face {
  font-family: DMSans_regular;
  src: url(./Components/Fonts/DMSans-Regular.ttf);
}
@font-face {
  font-family: DMSans_medium;
  src: url(./Components/Fonts/DMSans-Medium.ttf);
}
@font-face {
  font-family: Allerta;
  src: url(./Components/Fonts/AllertaStencil-Regular.ttf);
}

body {
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  color: white;
  /* background-color: #172d13; */
  background-color: #305478;
}
