.skills_list {
  width: 1248px;
  display: flex;
  /* flex-direction: row; */
  justify-content: space-around;
  flex-wrap: wrap;
  /* gap: 15px; */
  color: #101015;
  font-family: Allerta;
  padding: 20px 0;
  margin: 16px auto;
  background-color: #426486;
  border-radius: 16px;
  height: auto;
}
.skills_list h3 {
  text-align: left;
  padding-bottom: 15px;
}
.skills_list h4 {
  font-weight: 500;
  font-size: 19px;
  line-height: 13px;
  color: #101015;
  font-family: Allerta;
  text-align: left;
}
.skills {
  width: auto;
  display: flex;
  flex-direction: column;
}
.hard_skills {
  width: 500px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.soft_skills {
  width: 500px;
  display: flex;
  flex-direction: column;

  gap: 20px;
}
@media (width < 600px) {
  .skills_list {
    flex-direction: column;
    align-items: center;
    width: 590px;
    gap: 30px;
  }
  .hard_skills {
    width: 590px;
  }
  .soft_skills {
    width: 590px;
  }
}
@media (width < 480px) {
  .skills_list {
    width: 470px;
  }
  .hard_skills {
    width: 460px;
  }
  .soft_skills {
    width: 460px;
  }
}
@media (width < 320px) {
  .skills_list {
    width: 300px;
    gap: 30px;
  }
  .hard_skills {
    width: 290px;
  }
  .soft_skills {
    width: 290px;
  }
}
