.about_me {
  background: #426486;
  max-width: 1248px;
  border-radius: 16px;
  display: flex;
  background-image: url(./Assets/bgmain3.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: color-dodge;
  background-attachment: scroll;
  margin: auto;
}
.info_me {
  margin: 50px 50px;
  height: auto;
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;
  background-color: #212121;
  opacity: 0.6;
  border-radius: 16px;
}
.name {
  /* max-width: 1248px; */
  height: 84px;
}
.box_name {
  width: auto;
  height: 50px;
  border-radius: 16px;
  padding: 10px;
  border-right: 2px solid #e9c564;
  background-color: #212121;
  position: relative;
  top: 340px;
  left: -10px;
  z-index: 3;
  opacity: 0.6;
}
.box_name h3 {
  font-family: DMSans_medium;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
}

.info_me h1 {
  font-family: DMSans_regular;
  color: #f5f5f5;
  font-weight: 400;
  font-size: 35px;
  /* line-height: 40.5; */
}
.info_me h3,
a {
  font-family: DMSans_regular;
  color: #f5f5f5;
  font-weight: 400;
  font-size: 25px;
  text-decoration: none;
}
.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacts a:hover {
  color: #e4ee53;
}
.pic_me img {
  height: 446px;
  position: relative;
  left: 80px;
  padding-top: 30px;
  z-index: 2;
}
.pic_me {
  background-image: url(./Assets/FX_logo4.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 848px;
  padding-top: 35px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  z-index: 1;

  border-radius: 300px;
}
.what_i_can {
  width: 400px;
  text-align: center;
  padding: 20px;
}
@media (width < 600px) {
  .about_me {
    flex-direction: column;
    width: 590px;
    margin-bottom: 30px;
  }
  .what_i_can {
    width: 520px;
    margin: auto;
  }
  .info_me {
    margin: auto;
    text-align: center;
  }
  .pic_me {
    width: 590px;
  }
  .box_name {
    left: -145px;
    top: 375px;
    height: 45px;
  }
}
@media (width < 480px) {
  .about_me {
    flex-direction: column;
    width: 470px;
  }
  .what_i_can {
    width: 370px;
  }
  .info_me {
    margin: auto;
    text-align: center;
  }
  .pic_me {
    width: 470px;
  }
  .box_name {
    left: -145px;
    top: 375px;
    height: 45px;
  }
}
@media (width < 320px) {
  .about_me {
    width: 290px;
    flex-direction: column;
  }
  .what_i_can {
    width: 290px;
    padding: 0;
  }
  .info_me {
    margin: auto;
    text-align: center;
  }
  .pic_me {
    /* overflow: visible; */
    width: 290px;
  }
  /* .box_name {
    left: -145px;
    top: 375px;
    height: 65px;
  } */
}
