.progress {
  background: #6680b3;
  justify-content: flex-start;
  border-radius: 100px;
  align-items: center;
  position: relative;
  padding: 0 5px;
  display: flex;
  height: 20px;
  width: 500px;
  color: #f5f5f5;
}

.skills_name {
  text-align: right;
  font-size: 13px;
  color: #101015;
}
.skills_name h4 {
  color: #f5f5f5;
  padding-bottom: 5px;
}
@media (width < 600px) {
  .progress {
    width: 570px;
  }
}
@media (width < 480px) {
  .progress {
    width: 440px;
  }
}
@media (width < 320px) {
  .progress {
    width: 280px;
  }
}

